
import { defineComponent, SetupContext } from 'vue'

export default defineComponent({
  name: 'tab',
  props: {
    label: {
      type: String,
      required: true
    },
    id: {
      type: Number,
      required: true
    },
    active: {
      type: Boolean,
      required: true
    }
  },
  setup (props, context: SetupContext) {
    return {
      onClick: () => {
        context.emit('select-tab', props.id)
      }
    }
  }
})
